.full-width.dropdown {
    position: static;
    top-margin: -10px;
}
.full-width.dropdown > .dropdown-menu {
    left: 0;
    right: 0;
    position: absolute;
}
.full-width.dropdown > .dropdown-menu > li > a {
   white-space: normal; 
}

/*dropdown .dropdown-menu .dropdown-item:active, .dropdown .dropdown-menu .dropdown-item:hover {
	background-color: black !important;
}*/



/*.dropdown:hover .dropdown-menu{
    display: block;
}*/

.dropdown-item.active, .dropdown-item:active {
	background-color: white !important;
}

.dropdown-menu a{
 color: #111111 !important;
}
.dropdown-menu a:hover{
 background: #014464!important;
 background: linear-gradient(to left, #0272a7, #013953);
 background: -moz-linear-gradient(to left, #0272a7, #013953)!important;
 background: -webkit-gradient(linear, 100% 100%, 0% 0%, from(#0272a7), to(#013953))!important;
 color: #f8f9fa !important;
 transition: all 0.2s ease-in-out; 
}

a:link {
  color: black;
}

/* visited link */
a:visited {
  color: black;
}

/* mouse over link */
a:hover {
  color: black;
}

/* selected link */
a:active {
  color: black;
}
.carousel-control-prev-icon {
  /*size */
    height: 50px;
    width: 50px;
    /*symbol */
    color: #fff;
    line-height: 70px;
    /*vertical position */
    top: 50%;
    margin-left: 10px;
}

.carousel-control-next-icon {
  /*size */
    height: 50px;
    width: 50px;
    /*symbol */
    color: #fff;
    line-height: 70px;
    /*vertical position */
    top: 50%;
    margin-right: 10px;
}

.carousel-control-prev {
  width: 40px;
}

.carousel-control-next {
  width: 40px;
}
.current-page {
  font-size: 1.5rem;
  vertical-align: middle;
}

.country-card-container {
  height: 60px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.country-name {
  font-size: 0.9rem;
}

.country-region {
  font-size: 0.7rem;
}

.country-page,
.country-name,
.country-region {
  line-height: 1;
}

ul.pagination {
  margin-top: 0;
  margin-bottom: 0;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}
ul.pagination li.page-item.active a.page-link {
  color: #445565 !important;
  background-color: #e3e7eb !important;
  border-color: #ced4da !important;
}
ul.pagination a.page-link {
  padding: 0.75rem 1rem;
  min-width: 3.5rem;
  text-align: center;
  box-shadow: none !important;
  border-color: #ced4da !important;
  color: #6b88a4;
  font-weight: 900;
  font-size: 1rem;
}
ul.pagination a.page-link:hover {
  background-color: #f4f4f4;
}

